import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function Form() {

    const navigate = useNavigate(); 

    const [symptoms, setSymptoms] = useState({
        headache: "",
        strangeFeeling: "",
        seizures: "",
        problemsFindingWords: "",
        weaknessInArms: "",
        nausea: "",
        visionProblems: "",
        sensationProblems: "",
        strangeSmells: ""
    });

    const [output, setOutput] = useState("");

    // Function to calculate fuzzy logic output
    const evaluateFuzzyLogic = () => {
        const { headache, strangeFeeling, seizures, problemsFindingWords, weaknessInArms, nausea, visionProblems, sensationProblems, strangeSmells } = symptoms;

        // Initialize scores based on user input
        const scores = {
        headache: getScore(headache, 60.58),
        strangeFeeling: getScore(strangeFeeling, 50.60),
        seizures: getScore(seizures, 40.27),
        problemsFindingWords: getScore(problemsFindingWords, 42.97),
        weaknessInArms: getScore(weaknessInArms, 44.12),
        nausea: getScore(nausea, 40.74),
        visionProblems: getScore(visionProblems, 44.81),
        sensationProblems: getScore(sensationProblems, 35.48),
        strangeSmells: getScore(strangeSmells, 32.35)
        };

        // Calculate the overall decision
        const totalScore = Object.values(scores).reduce((sum, score) => sum + score, 0);
    
        if (totalScore >= 100) {
        setOutput("Visit Doctor");
        } else {
        setOutput("Do Not Visit Doctor");
        }
    };

    // Function to get membership score based on input
    const getScore = (input: any, yesValue: any) => {
        switch (input) {
        case "yes":
            return yesValue;
        case "medium":
            return yesValue / 2; // Assigning medium as half of yes value
        case "no":
            return 0;
        default:
            return 0; // Handle invalid inputs
        }
    };

    const handleChange = (e: any) => {
        setSymptoms({
        ...symptoms,
        [e.target.name]: e.target.value
        });
    };





    return (

        <div className='w-full flex justify-center'>

        <div className='bg-violet-700 p-7 mt-4 rounded-3xl px-12'>

        <h1 className='text-white text-3xl font-bold mb-7'>Fuzzy Logic Tumor Checkup System</h1>

            <div>
                <label className='font-semibold text-xl text-white'>Do you have a headache? </label>
                <select name="headache" onChange={handleChange} required className=''>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>
            
            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have a strange feeling in your head? </label>
                <select name="strangeFeeling" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have seizures? </label>
                <select name="seizures" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have problems finding the right words? </label>
                <select name="wordProblems" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you feel weakness in arms and legs? </label>
                <select name="weakness" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have nausea or vomiting? </label>
                <select name="nausea" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have problems with your vision? </label>
                <select name="visionProblems" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you have problems with sensation in your hands? </label>
                <select name="sensationProblems" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <div className='mt-5'>
                <label className='font-semibold text-xl text-white'>Do you experience strange smells (that others do not smell)? </label>
                <select name="strangeSmells" onChange={handleChange} required>
                    <option value="">Select</option>
                    <option value="yes">Yes</option>
                    <option value="yes">Medium</option>
                    <option value="no">No</option>
                </select>
            </div>

            <button className='mt-5 font-semibold w-auto select-none rounded-2xl bg-gray-200 py-2 px-2 text-center align-middle font-sans text-lg uppercase text-indigo-700 shadow-md transition-all hover:shadow-lg hover:shadow-gray-900/20 active:opacity-[0.85] disabled:pointer-events-none' onClick={evaluateFuzzyLogic}>Check Symptoms</button>

        {output && <h2 className='mt-5 font-bold text-white text-3xl'>{output}</h2>}

        {output && <div className='mt-5 font-semibold'>
                        <button
                            className="font-semibold w-auto select-none rounded-2xl bg-gray-200 py-2 px-2 text-center align-middle font-sans text-lg uppercase text-indigo-700 shadow-md transition-all hover:shadow-lg hover:shadow-gray-900/20 active:opacity-[0.85] disabled:pointer-events-none "
                            onClick={() => {
                                navigate("/uploadMRI") 
                            }}
                        >Upload your MRI </button>
                    </div>
        }

        </div>

        </div>
    );
}

export default Form;