import {
    PushSpinner, TraceSpinner, RainbowSpinner,
    RingSpinner, SwishSpinner, PongSpinner,
    MetroSpinner, JellyfishSpinner
} from "react-spinners-kit";


function AdminMRIresults() { 

    return (
        <>

        <div className="w-full flex justify-center mt-32">

            <div className="spinner">
                <JellyfishSpinner size={200} color="purple"
                    loading={true} />
            </div>

        </div>

        <div className="w-full flex justify-center mt-16">
            <h1 className="font-semibold text-purple-900 text-3xl">scanning...</h1> 
        </div>

        </>
    )

}

export default AdminMRIresults; 